import {Observer} from "./observer"

export class Dep{
  constructor(vm){
    this.vm = vm
  }

  depPipe(){
    let depPipe = this.vm.getDep([this.vm.pipe], this.vm.pipe, this.vm.treeId())
    Observer.fire('vm_pull_dep', {pipe: this.pipe, vm: this.vm, dep: this, depPipe: depPipe})
    return depPipe
  }
}

export class undefinedDep extends Dep{

  pull(){
    Observer.fire('vm_pull_dep', {pipe: this.pipe, vm: this.vm, dep: this, result: undefined})
    return undefined
  }
}



