import {Block} from "./block"
import {Map, List} from "immutable"


export class Pipe extends Block{
  constructor(content){
    super(content)
    this.patches = []
    this.deps = Map()
    this.desc = ''
  }

  addPatch(...patches){
    Array.prototype.push.apply(this.patches, patches)
    this.patches.sort((a,b) => {
      if (a.from.i < b.from.i){
        return -1
      }
      if (a.from.i > b.from.i){
        return 1
      }
      return 0
    })
  }

  // setArgs(i, requestedPipe, depPipe){
  //   this.args = this.args.setIn([i, requestedPipe], depPipe)
  // }
  //
  // getArgs(i){
  //   return this.args.get(i)
  // }

  // [i, block]
  // [originPipe]
  setDep(origin, requestedPipe, depPipe){
    this.deps = this.deps.setIn([List(origin)].concat(requestedPipe), depPipe)
  }

  getDep(origin, pipe) {
    return this.deps.getIn([List(origin)].concat(pipe))
  }

  removeDep(origin, pipe){
    this.deps = this.deps.deleteIn([List(origin)].concat(pipe))
  }
}



