import {Observer} from './observer'
import {Dep} from "./dep"
import { rootVM } from "./rootVm"
import {Pipe} from "./pipe"

export class VM {
  constructor(pipe, parent, origin){
    if (!pipe){
      debugger
    }
    this.pipe = pipe
    this.parent = parent || rootVM
    this.origin = origin || [pipe]
  }

  pull(){
    // let dep = this.getDep([this.pipe], this.pipe, this.treeId())
    let dep = new Dep(this)
    // this.notifyPull(pipe, (dep||{}).pipe)
    return this._push(this.pipe, 0, this.pipe, dep)
  }

  toValue(){
    return this._toValue(this.pull())
  }

  notifyPull(pipe, origin){
    Observer.fire('vm_pull', {vm: this, depPipe: pipe, originPipe: origin})
  }

  _push(block, i, pipe, input){
    Observer.fire('vm_push', {vm: this, pipe: pipe, block: block, i: i, input: input})
    let result
    // next outgoing patch in block
    let patch = pipe.patches.find((patch) => {
      return patch.from.block === block && (0 === i ? i <= patch.from.i : i < patch.from.i) && patch.valid(this, pipe)
    })
    if (patch) {
      let head = this._pushToChunk(block, i, patch.from.i, input, pipe)
      result = this._push(patch, 0, pipe, head)
    } else {
      let chunkResult =  this._pushToChunk(block, i, block.content.length, input, pipe)
      if ('Patch' === block.constructor.name) {
        result = this._push(block.to.block, block.to.i, pipe, chunkResult)
      } else {
        // result = this._whileOrReturn(block, chunkResult)
        result = chunkResult
      }
    }
    Observer.fire('vm_push_result', {vm: this, pipe: pipe, block: block, i: i, input: input, result: result})
    if (block === pipe) {
      Observer.fire('vm_result', {vm: this, pipe: this.pipe, result: result})
    }
    return result
  }

  // _whileOrReturn(pipe, result){
  //   if (!this.primaryPipe) this.primaryPipe = pipe
  //   if (pipe === this.primaryPipe) {
  //     let whileDep = pipe.getDep(whileCond, this)
  //     if (whileDep && whileDep.pipe !== whileCond && this.pull(whileDep.pipe)) {
  //       return new VM(pipe, this.parent, this)._push(pipe, 0, pipe, result)
  //     } else {
  //       return result
  //     }
  //   } else return result
  // }

  _pushToChunk(block, float_i, float_j, input, pipe){
    let data = {vm: this, pipe: pipe, block: block, fromI: float_i, toI: float_j, input: input}
    Observer.fire('vm_push_chunk', data)

    let i = Math.floor(float_i)
    let j = Math.floor(float_j)

    var result = input
    for(var k = i; k < j && k < block.content.length; k++ ){
      result = this._pushToIndex(block, k, result)
      // data for observer
      let data = {vm: this, pipe: pipe, block: block, i: k, result: result}
      Observer.fire('vm', data)
    }
    return result
  }

  _pushToIndex(block, i, input){
    let atom = block.content[i]
    let type = atom.constructor.name
    if (atom instanceof Dep) {
      return atom
    }
    else if (atom instanceof Pipe) {
      let childVM = new VM(atom, this, [i, block])

      let data = {vm: childVM, block: atom, input: input}
      Observer.fire('vm_push_child', data)

      return childVM._push(atom, 0, atom, input)
    } else if ('String' === type) {
      let inputValue = this._toValue(input)
      if ('undefined' === typeof inputValue)
        return atom
      else
        return inputValue.toString().concat(atom)
    } else if ('Number' === type) {
      return atom
    }
    else {
      return atom.push(input, this, [i, block])
    }
  }

  getDep(origin, requestedPipe, requesterId){
    let depPipe = this.pipe.getDep(origin, requestedPipe)
    if (!depPipe) {
      if (origin.length > 0) {
        depPipe = this.getDep([], requestedPipe, requesterId)
      } else {
        depPipe = this.parent.getDep(this.origin, requestedPipe, requesterId)
      }
    }

    Observer.fire('vm_getdep', {requesterId: requesterId, vm: this, origin: origin, requestedPipe: requestedPipe, depPipe: depPipe})

    return depPipe

    // let argDepPipe = this.args.get(pipe)
    // if (argDepPipe){
    //   result = new Dep(argDepPipe, currentVM)
    //   Observer.fire('vm_parent_getdep', {type: 'args', pipe: pipe, vm: this, result: result})
    // } else {
    //   if (this.parent) {
    //     result = this.parent.getDep(pipe, currentVM)
    //     Observer.fire('vm_parent_getdep', {type: 'parent', pipe: pipe, vm: this, result: result})
    //   } else {
    //     result = new undefinedDep(pipe, currentVM)
    //   }
    // }
    // return result
  }

  _toValue(input){
    return VM.toValue(input)
  }

  static toValue(input){
    let result

    if (input instanceof Dep){
      let depPipe = input.depPipe()
      if (depPipe) {
        result = new VM(depPipe, input.vm, [depPipe]).toValue()
      } else {
        result = undefined
      }
    } else {
      result = input
    }

    return result
  }

  toString(input){
    let val = this._toValue(input)
    if('undefined' === typeof val) {
      return ''
    } else {
      return val
    }
  }

  treeId(){
    if ('number' === typeof this.origin[0])
      return this.parent.treeId().concat(this.origin).concat(this.pipe)
    else
      return this.parent.treeId().concat(this.pipe)
  }
}



