import React from 'react';
import SplitPane from 'react-split-pane';
import './pane.css'
import TextIDE from './textIde'
import Layers from "./layers"
import Navigation from './navigation'
import PipeHelper from "../core/helpers/pipeHelper"
import { VM } from '../core/vm'

const Pane = (props) => {

  let pipeHelper = new PipeHelper(props.model.currentVM.pipe, props.model.currentVM, ()=>{
    props.model.rootVM.toValue()
  }, ()=>{
    let rootVM = props.model.currentVM
    while (rootVM.parent instanceof VM) { rootVM = rootVM.parent }
    rootVM.toValue()
  })

  return (
      <SplitPane split="vertical" minSize={50} maxSize={600} defaultSize={300} className="primary">
        <SplitPane split="horizontal" defaultSize={300}>
          <div>
            <h3><a href="https://lerd.gdn">LERD</a></h3>
            <Navigation model={props.model} controller={props.controller} pipeHelper={pipeHelper} />
            <button onClick={e => {
              let pipe = props.controller.createPipe()
              props.controller.setActiveVM(new VM(pipe))
            }}>Create Pipe</button>
          </div>
          <div>
            <button onClick={props.controller.createLayer}>Create Layer</button>
            <Layers model={props.model} controller={props.controller} pipeHelper={pipeHelper} />
          </div>
        </SplitPane>
        <div>
          <div>
            <input defaultValue={props.model.currentVM.pipe.desc} onBlur={ e=> {props.controller.changeCurrentPipeDesc(e.target.value)}} />
          </div>
          <TextIDE model={props.model} controller={props.controller} pipeHelper={pipeHelper}/>
          <div className="hints">
            <h5>Keyboard shortcuts:</h5>
            <ul>
              <li>Cmd + s : Commit changes</li>
              <li>Ctrl + i : Insert pipe</li>
              <li>Ctrl + / : Change pipe</li>
              <li>Ctrl + c : Create pipe in pipe selector</li>
            </ul>
          </div>

        </div>
      </SplitPane>
  );
};

export default Pane



