import React from 'react'
import { Pipe } from '../core/pipe'
import { VM } from '../core/vm'
import Pane from './pane'
import {Ptr} from "../core/ptr"
import {Patch} from "../core/patch"
import {
  concatA,
  concatB,
  incA,
  decA,
  bypass,
  equialsA,
  equialsB,
  hiPipe,
  locationHash,
  fetchA,
  fetchB,
  terminator,
    isEmpty,
    emptyString
} from '../core/base'
import Override from "../core/override"

export default class RootContainer extends React.Component {
  constructor (props) {
    super(props)


    // let pipe = new Pipe([])
    //
    // let testlayer = new Pipe([])
    // testlayer.desc = 'test Layer'
    // testlayer.new = true
    let aboutKey = new Pipe('about')
    aboutKey.desc = 'about page key'
    aboutKey.setDep([], aboutKey, terminator)

    let rootKey = new Pipe([''])
    rootKey.desc = 'root page key'
    rootKey.setDep([], rootKey, terminator)

    let pathKey = new Pipe('path')
    pathKey.desc = 'about page key'
    pathKey.setDep([], pathKey, terminator)

    let langKey = new Pipe('lang')
    langKey.desc = 'lang page key'
    langKey.setDep([], langKey, terminator)

    let inpipe = new Pipe('abc')
    inpipe.desc = 'ABC'
    let isAbout = new Pipe([locationHash, fetchA, equialsA])
    isAbout.desc = 'is about page'
    isAbout.color = 'red'
    isAbout.setDep([], fetchB, pathKey)
    isAbout.setDep([], equialsB, aboutKey)

    let currentPath = new Pipe([locationHash, fetchA])
    currentPath.desc="current path"
    currentPath.setDep([], fetchB, pathKey)

    let path = new Pipe([])
    path.desc = "path value"

    let lang = new Pipe([])
    lang.desc = "lang value"

    let currentLang= new Pipe([locationHash, fetchA])
    currentLang.desc = 'Current lang'
    currentLang.setDep([], fetchB, langKey)

    let urlHelper = new Pipe('#path='.split('').concat(concatA).concat('&lang='.split('')).concat(concatA))
    urlHelper.desc = "url helper"
    urlHelper.setDep([6, urlHelper], concatB, path)
    urlHelper.setDep([13, urlHelper], concatB, lang)

    let condition = new Pipe([])
    condition.desc = 'layer1'
    condition.setDep([], condition, isAbout)

    let emptyPipe = new Pipe([])
    emptyPipe.desc = "EmptyPipe"
    emptyPipe.setDep([], emptyPipe, terminator)
    // emptyPipe.setDep([], concatB, hiPipe)
    emptyPipe.new = true
    // let patcht = new Patch([], new Ptr(emptyPipe, 1.4), new Ptr(emptyPipe, 2.6), tmpPipe)
    // emptyPipe.addPatch(patcht)
    // emptyPipe.setDep([0, patcht], concatB, hiPipe)

    let pipe = new Pipe('123'.split('').concat(urlHelper).concat(urlHelper))
    pipe.desc = 'Main Pipe'
    pipe.setDep([3, pipe], path, aboutKey)
    pipe.setDep([4, pipe], path, rootKey)
    // pipe.setDep([0, pipe], fetchB, emptyPipe)
    // pipe.setDep([], pipe, locationHash)
    pipe.setDep([], path, currentPath)
    pipe.setDep([], lang, currentLang)

    let patch = new Patch('a', new Ptr(pipe, 0.9), new Ptr(pipe, 1.2), condition)
    pipe.addPatch(patch)
    let patch1 = new Patch('b', new Ptr(pipe, 1.9), new Ptr(pipe, 2.2), condition)
    pipe.addPatch(patch1)
    let patch2 = new Patch('a', new Ptr(pipe, 2.9), new Ptr(pipe, 3.2), condition)
    pipe.addPatch(patch2)

    pipe.layers = [condition]

    // pipe.addPatch(new Patch('patch', new Ptr(pipe, 1.4), new Ptr(pipe, 1.4), isAbout))
    // pipe.addPatch(new Patch('prefix', new Ptr(pipe, 0.9), new Ptr(pipe, 1.2)))
    // pipe.addPatch(new Patch('sufix', new Ptr(pipe, 1.9), new Ptr(pipe, 2.2)))

    // let pipe3 = new Pipe([concatA])
    // let pipe2 = new Pipe([concatA])
    // let pipe1 = new Pipe([pipe2])
    // pipe1.desc = 'pipe1'
    // pipe2.desc = 'pipe2'


    let colors = [
        '#b00',
        '#0b0',
        '#00b',
        '#bb0',
        '#0bb',
        '#b0b',
    ]
    let vm = new VM(emptyPipe)
    this.state = {
      rootPipes: [emptyPipe],
      rootPipe: emptyPipe,
      pipes: [incA, decA, bypass, pipe, equialsA, hiPipe, locationHash, fetchA, isAbout, concatA, terminator, isEmpty, emptyString],
      rootVM: vm,
      currentVM: vm,
      currentLayer: null,
      activeSuggest: null,
      // activeSuggest: new Ptr(pipe, 1),
      textide: null
    }
    let self = this

    this.controller = {
      changeCurrentPipeDesc: function(value){
        self.state.currentVM.pipe.desc = value
        self.setState({currentVM: self.state.currentVM})
      },
      setDep: function (context, pipe, pipeDep) {
        context.setDep(pipe, pipeDep)
        self.setState({currentVM: self.state.currentVM})
      },
      removeDep: function (context, pipe) {
        context.removeDep(pipe)
        self.setState({currentVM: self.state.currentVM})
      },
      createPipe: () => {
        let pipe = new Pipe([])
        pipe.desc = prompt("Please enter pipe name", 'New Pipe')  || "New pipe";
        pipe.new = true
        pipe.setDep([], pipe, terminator)
        this.state.pipes.push(pipe)
        // self.state.currentVM.context.setDep(pipe, new Pipe(""))
        // self.setState({currentVM: self.state.currentVM})
        // self.controller.setActivePipe(pipe)
        return pipe
      },
      setActiveVM: (vm) => {
        if (!vm){
          debugger
        }
        self.setState({
          currentVM: vm,
          currentLayer: null
        })
      },
      setActiveLayer: (layer) => {
        self.setState({currentLayer: layer})
      },
      createLayer: () => {
        self.state.currentVM.pipe.layerColors = self.state.currentVM.pipe.layerColors || colors

        let layer = new Pipe([])
        layer.new = true
        layer.index = (self.state.currentVM.pipe.layers||[]).length
        layer.color = self.state.currentVM.pipe.layerColors.shift() || 'black'
        layer.desc = prompt("Please enter layer name", 'New Layer')  || 'New Layer'
        Override.set(layer, true, false)

        self.state.currentVM.pipe.layers = (self.state.currentVM.pipe.layers||[]).concat(layer)

        self.setState({currentLayer: layer})
      },
      toggleLayer: (layer, value, actualValue) => {
        Override.set(layer, !!value, !!actualValue)
        // this.state.currentVM.context.setDep(layer, new Pipe( value ? '1' : ''), Context.refCurrentResult)
        self.setState({currentVM: this.state.currentVM})
      },
      setActiveSuggest(ptr){
        self.state.textide.setActiveSuggest(ptr)
      },
      changeActiveSuggest(pipe){
        self.state.textide.changePipe(pipe)
        this.setActiveSuggest(null)
      },
      reload(callback){
        self.setState({currentVM: self.state.currentVM}, () => {
          if (callback) {
            callback(self.state.textide.props.pipeHelper)
          }
        })
      }
    }

    window.addEventListener("hashchange", ()=>{self.setState({currentVM: self.state.currentVM})}, false);
  }

  render() {
    return <div>
      <Pane model={this.state} controller={this.controller}/>
    </div>
  }
}



