import React from 'react';
import {Editor, EditorState, convertToRaw, SelectionState, Modifier, Entity} from 'draft-js';
import { VM } from '../core/vm'
import './textIde.css'
import ObjectInspector from 'react-object-inspector';
import getDecorator from './decorator'
import { bypass, tmpPipe } from '../core/base'
import {List, Map} from 'immutable'
import DepControl from "./depControl"

const styleMap = {
  'STRIKETHROUGH': {
    textDecoration: 'line-through',
  },
};



export default class TextIde extends React.Component {
  constructor(props) {
    super(props);
    props.model.textide = this
    // this.state = {editorState: EditorState.createEmpty()};


    // let pipeHelper = new PipeHelper(this.props.model.currentPipe, this.props.model.currentVM)
    // let state = pipeHelper.toDraft(decorator)
    //
    // this.state = {
    //   pipeHelper: pipeHelper,
    //   editorState: state, //this.pipeHelper.initialState
    //   focusOffset: 0,
    // };
    this.state = {
      inspector: true,
      activeSuggest: null,
    }

    this.handlePastedText = (text, html, editorState) => {
      console.log('paste')
      console.log(text)
      console.log(html)

      let newContent = Modifier.replaceText(editorState.getCurrentContent(), editorState.getSelection(), text);
      let newState = EditorState.push(editorState, newContent, 'insert-characters');
      this.setState({editorState: newState})
      return true;
    }

    this.handleBeforeInput = (chars, editorState) => {
      console.log('before Input ------------------------------')
      console.log(chars)
      return false
    }

    this.rerenderEntities = (editorState) => {
      // let focusOffset = 0
      let focusOffset = editorState.getSelection().getFocusOffset()
      let contentState = editorState.getCurrentContent()

      let blockKey = contentState.getFirstBlock().getKey()
      let key = contentState.getFirstBlock().getEntityAt(focusOffset)
      let targetRange = new SelectionState({
        anchorKey: blockKey,
        anchorOffset: focusOffset,
        focusKey: blockKey,
        focusOffset: focusOffset,
      });

      let newContentState = Modifier.applyEntity(contentState, targetRange, key);
      return EditorState.push(editorState, newContentState, 'apply-entity');
    }

    this.onChange = (editorState) => {
      let type = editorState.getLastChangeType()
      console.log('draft onChange ------------------------------', type)
      // console.log(type)
      // console.log(editorState.getSelection().getFocusKey())
      // console.log(editorState.getSelection().getFocusOffset())
      // console.log(editorState.getSelection().getStartOffset())
      // console.log(editorState.getSelection().getEndOffset())
      // console.log(editorState.getCurrentContent().getSelectionAfter())
      // console.log(editorState.getCurrentInlineStyle())
      // console.log(editorState.getSelection().serialize())
      // console.log(content)

      // if ('split-block' == type ) return;
      // console.log(content.getFirstBlock().getCharacterList())
      // console.log(content.getFirstBlock().getCharacterList().toJS())
      // console.log(content.getFirstBlock().getData().toJS())
      // console.log(content.getSelectionBefore().toJS())
      // console.log(content.getSelectionAfter().toJS())
      // console.log('content')
      // console.log(convertToRaw(content))
      // console.log(content.getEntityMap())
      // console.log(content.getBlockMap())
      // console.log(editorState.getSelection())
let newState
      if (type) {
        let newContentState = this.performUpdate(editorState)
        newState = EditorState.push(editorState, newContentState, 'apply-entity');
      } else {
        newState = editorState
      }

      this.setState({ editorState: newState})
    }

    this.performUpdate = (editorState) => {
      this.state.pipeHelper.fromDraft(editorState.getCurrentContent(), tmpPipe)
      return this.state.pipeHelper.updateDraft(editorState.getCurrentContent())
    }

    this.onCopy = (a) => {
      debugger
    }

    this.onFocus = (e) => {
      console.log('draft onfocus')
    }

    this.onBlur = (e) => {
      console.log('draft onblur')
    }

    this.logState = () => {
      const content = this.state.editorState.getCurrentContent();
      console.log(convertToRaw(content));
      console.log(JSON.stringify(convertToRaw(content)));
    };

    this.onKeyDown = (e) => {
      console.log(`key: ${e.key}`)
      if (27 === e.keyCode){
        e.preventDefault()
        e.stopPropagation()

        let focusOffset = this.state.editorState.getSelection().getFocusOffset()
        let contentState = this.state.editorState.getCurrentContent()
        let blockKey = contentState.getFirstBlock().getKey()
        let key = contentState.getFirstBlock().getEntityAt(focusOffset)
        // let data = this.state.editorState.getCurrentContent().getEntity(key).getData()

        // let newContentState = contentState.mergeEntityData(key, {activeSuggest: true})
        let targetRange = new SelectionState({
          anchorKey: blockKey,
          anchorOffset: focusOffset,
          focusKey: blockKey,
          focusOffset: focusOffset,
        });
        let newContent = Modifier.applyEntity(contentState, targetRange, key);
        let newState = EditorState.push(this.state.editorState, newContent, 'apply-entity');
        this.onChange(EditorState.forceSelection(newState, targetRange))

        this.setActiveSuggest(null)
      }
      else if ('s' === e.key && e.metaKey) {
        e.preventDefault()
        e.stopPropagation()
        const content = this.state.editorState.getCurrentContent();
        if (content.getBlockMap().count() > 1) {
          alert('Editor error: block count more then 1')
        } else {


          let newPipe = this.state.pipeHelper.fromDraft(this.state.editorState.getCurrentContent(), this.props.model.currentLayer)
          newPipe.new = false
          // this.setState({pipeHelper: null})
          this.props.controller.setActiveVM(this.state.pipeHelper.vm)
        //   let context = new Context()
        //   let pipeHelper = new PipeHelper(newPipe, new VM(context))
        //   let state = this.pipeHelper.toDraft(decorator)
        //
        //   this.setState({
        //     pipeHelper: pipeHelper,
        //     editorState: state,
        //     focusOffset: this.state.editorState.getSelection().getFocusOffset()})
        }
      } else if ('/' === e.key && e.ctrlKey){
        e.preventDefault()
        e.stopPropagation()
        let focusOffset = this.state.editorState.getSelection().getFocusOffset()
        let contentState = this.state.editorState.getCurrentContent()
        let blockKey = contentState.getFirstBlock().getKey()
        let key = contentState.getFirstBlock().getEntityAt(focusOffset)
        // let data = this.state.editorState.getCurrentContent().getEntity(key).getData()

        // let newContentState = contentState.mergeEntityData(key, {activeSuggest: true})
        let targetRange = new SelectionState({
          anchorKey: blockKey,
          anchorOffset: focusOffset,
          focusKey: blockKey,
          focusOffset: focusOffset,
        });
        let newContent = Modifier.applyEntity(contentState, targetRange, key);
        let newState = EditorState.push(this.state.editorState, newContent, 'apply-entity');

        this.onChange(newState)
        // Entity.replaceData(key, {activeSuggest: true})
       this.setActiveSuggest(key)
      } else if ('i' === e.key && e.ctrlKey) {
        e.preventDefault()
        e.stopPropagation()
        let content = this.state.editorState.getCurrentContent()
        let contentStateWithEntity = content.createEntity('TOKEN', 'IMMUTABLE', {atom: bypass, vm: new VM(bypass, this.props.model.currentVM, [])}) //content.createEntity
        const newEntityKey = contentStateWithEntity.getLastCreatedEntityKey();

        let blockKey = content.getFirstBlock().getKey()
        let focusOffset = this.state.editorState.getSelection().getFocusOffset()
        let targetRange = new SelectionState({
          anchorKey: blockKey,
          anchorOffset: focusOffset,
          focusKey: blockKey,
          focusOffset: focusOffset,
        });
        let newContent = Modifier.insertText(contentStateWithEntity, targetRange, '-', undefined, newEntityKey);
        let newState = EditorState.push(this.state.editorState, newContent, 'insert-characters');
        // this.setState({editorState: newState})
        this.onChange(EditorState.forceSelection(newState, targetRange))
        this.setActiveSuggest(newEntityKey)
      }
      else {
        // this.rangeControlRef.onKeyDown(e)
      }
    }
    this.storeRangeControlRef = this.storeRangeControlRef.bind(this);
    this.setDomEditorRef = ref => {
      this.domEditor = ref;
    }
    // this.pushContent = (content) => {
    //   var newState = EditorState.push(this.state.editorState, content, 'insert-characters');
    //   this.setState({editorState: newState})
    // }
    this.changePipe = (pipe) => {
      let content = this.state.editorState.getCurrentContent()
      let focusOffset = this.state.editorState.getSelection().getFocusOffset()
      // let entityKey = content.getFirstBlock().getEntityAt(focusOffset)
      let newEntityKey = Entity.create('TOKEN', 'IMMUTABLE', {atom: pipe, vm: new VM(pipe, this.props.model.currentVM, []), zaza: 2})

      // let newContent = content.replaceEntityData(entityKey, {atom: pipe, lala: 1})
      let blockKey = content.getFirstBlock().getKey()
      let targetRange = new SelectionState({
        anchorKey: blockKey,
        anchorOffset: focusOffset,
        focusKey: blockKey,
        focusOffset: focusOffset+1,
      });
      let newContent = Modifier.applyEntity(content, targetRange, newEntityKey);
      // debugger
      let newState = EditorState.push(this.state.editorState, newContent, 'insert-characters');
      // this.setState({editorState: newState})

      this.onChange(EditorState.forceSelection(newState, new SelectionState({
        anchorKey: blockKey,
        anchorOffset: focusOffset,
        focusKey: blockKey,
        focusOffset: focusOffset
      })))
      // this.setState({editorState: newState})
    }

    this.setActiveSuggest = (ptr) =>{
      this.setState({activeSuggest: ptr})
    }
    this.handleKeyCommand = this.handleKeyCommand.bind(this);
  }

  handleKeyCommand(command) {
    console.log('command')
    console.log(command)
    if (command === 'split-block') {
      // Perform a request to save your contents, set
      // a new `editorState`, etc.
      let blockKey = this.state.editorState.getCurrentContent().getFirstBlock().getKey()
      let focusOffset = this.state.editorState.getSelection().getFocusOffset()
      let targetRange = new SelectionState({
        anchorKey: blockKey,
        anchorOffset: focusOffset,
        focusKey: blockKey,
        focusOffset: focusOffset,
      });
      let newContent = Modifier.insertText(this.state.editorState.getCurrentContent(), targetRange, "\n");
      let newState = EditorState.push(this.state.editorState, newContent, 'insert-characters');
      // this.setState({editorState: newState})
      this.onChange(EditorState.forceSelection(newState, new SelectionState({
        anchorKey: blockKey,
        anchorOffset: focusOffset+1,
        focusKey: blockKey,
        focusOffset: focusOffset+1,
      })))

      return 'handled';
    }
    return 'not-handled';
  }

  static getDerivedStateFromProps(nextProps, prevState){
    console.log('PROPS------------------------------------------')
    // let pipeHelper = prevState && prevState.pipeHelper
    // if (true || !pipeHelper || pipeHelper.pipe !== nextProps.model.currentPipe) {

    let pipeHelper = nextProps.pipeHelper
    // }
    let decorator = getDecorator({model: nextProps.model, controller: nextProps.controller, pipeHelper: pipeHelper})
    let state = pipeHelper.toDraft(decorator)
    // if (prevState && prevState.editorState) {
    //   console.log(prevState.editorState.getSelection().getFocusOffset())
    //   targetRange = new SelectionState({
    //     anchorKey: blockKey,
    //     anchorOffset: prevState.editorState.getSelection().getAnchorOffset(),
    //     focusKey: blockKey,
    //     focusOffset: prevState.editorState.getSelection().getFocusOffset(),
    //   });
    //   state = EditorState.forceSelection(state, targetRange)
    // }
    // if (prevState && prevState.pipeHelper.pipe === nextProps.model.currentPipe) {
    //   return null;
    // }
    // let blockKey = state.getCurrentContent().getFirstBlock().getKey()
    // targetRange = new SelectionState({
    //   anchorKey: blockKey,
    //   anchorOffset: 2,
    //   focusKey: blockKey,
    //   focusOffset: 2,
    // });
    // state = EditorState.forceSelection(state, targetRange)

    let offset
    if (prevState.currentVM && prevState.currentVM.treeId() === nextProps.model.currentVM.treeId()){
      offset = prevState.editorState.getSelection().getFocusOffset()
    } else {
      offset = 0
    }

    let blockKey = state.getCurrentContent().getFirstBlock().getKey()
    let stateWithFocus = EditorState.forceSelection(state, new SelectionState({
      anchorKey: blockKey,
      anchorOffset: offset,
      focusKey: blockKey,
      focusOffset: offset,
    }))

    return {
      pipeHelper: pipeHelper,
      editorState: stateWithFocus,
      // currentPipe: nextProps.model.currentPipe
    }
  }

  storeRangeControlRef(ref) {
    this.rangeControlRef = ref
  }

  outGoingDepsNode(){
    let treePath = this.props.model.currentVM.parent.treeId().concat('origins').concat(List(this.props.model.currentVM.origin)).concat('deps')
    let depsNode = this.props.pipeHelper.tree.getIn(treePath)
    return depsNode
  }

  inComingDepsNodes(){
    let treePath = this.props.model.currentVM.treeId().concat('origins')
    let node = this.props.pipeHelper.tree.getIn(treePath)
    if (!node) return
    let origins = node.keySeq().toArray().filter((key) => {
      return key.size === 0
      // return !Number.isInteger(key.first())
    })
    let requestedPipes = origins.map((origin) => {
      return node.getIn([origin].concat('deps'))
    })
    return requestedPipes
  }

  render() {
    // let contextPipeKey = [this.props.model.currentVM.context, this.props.model.currentPipe]
    let treeId = this.props.model.currentVM.treeId()
    // let input =  this.props.pipeHelper.pipeInput.getIn(contextPipeKey, {})
    let pushed = this.props.pipeHelper.tree.getIn(treeId.concat('pushed'))
    // let deps = this.props.pipeHelper.pullMap.getIn(contextPipeKey)
    // let pulledBy = this.props.pipeHelper.pulledBy.getIn(contextPipeKey)
    let currentPipeResult = VM.toValue(this.props.pipeHelper.pipeResult(this.props.model.currentVM))
    let outGoingDepsNode = this.outGoingDepsNode()
    let inComingDepsNodes = this.inComingDepsNodes()
    return (
        <div onKeyDown={this.onKeyDown}>

          {
            pushed && <div>
                PUSHED:
                <div className="pipe-row">
                  <ObjectInspector
                      data={this.props.model.currentVM._toValue(pushed)}/>
                </div>
              </div>

          }

          {/*{*/}
            {/*deps && <div>*/}
            {/*PULLED:*/}
            {/*{ deps.map((d) => {*/}
              {/*return <div className="pipe-row">*/}
                  {/*<u onClick={() => {*/}
                    {/*this.props.controller.setActivePipe(d)*/}
                  {/*}}>{d.desc}</u>*/}
                  {/*<ObjectInspector*/}
                      {/*data={this.props.model.currentVM._toValue(this.props.pipeHelper.pipeInput.getIn([this.props.model.currentVM.context, d], {}).result)}/>*/}
                {/*</div>*/}
            {/*})}*/}
          {/*</div>*/}
          {/*}*/}

          {
            outGoingDepsNode && <div>
              UPLINKS:
              {(outGoingDepsNode||Map()).map((resolvedNode, requestedPipe)=>{
                if (resolvedNode.size > 1) {
                  throw new Error("tree structure faulure")
                }
                let resolvedPipe = resolvedNode.keySeq().first()
                let treeIds = resolvedNode.getIn([resolvedPipe])
                let localResolve = this.props.pipeHelper.pipe.getDep([], requestedPipe) && []
                if (!localResolve) localResolve = this.props.pipeHelper.pipe.getDep([this.props.pipeHelper.pipe], requestedPipe) && [this.props.pipeHelper.pipe]

                return <DepControl pipeHelper={this.props.pipeHelper} requestedPipe={requestedPipe} resolvedPipe={resolvedPipe} treeIds={treeIds} model={this.props.model} controller={this.props.controller} localResolve={localResolve}
                                   getOrigin={ () => { return [] }}
                />
              }).toSeq().toArray()}
            </div>
          }

          {
            inComingDepsNodes && <div>
              SIDELINKS:
              {(inComingDepsNodes || Map()).map((depsNode) => {
                return depsNode.map((resolvedNode, requestedPipe)=>{
                  if (resolvedNode.size > 1) {
                    throw new Error("tree structure faulure")
                  }
                  let resolvedPipe = resolvedNode.keySeq().first()
                  let treeIds = resolvedNode.getIn([resolvedPipe])
                  let localResolve = this.props.pipeHelper.pipe.getDep([], requestedPipe) && []
                  if (!localResolve) localResolve = this.props.pipeHelper.pipe.getDep([this.props.pipeHelper.pipe], requestedPipe) && [this.props.pipeHelper.pipe]

                  return <DepControl key={requestedPipe} pipeHelper={this.props.pipeHelper} requestedPipe={requestedPipe} resolvedPipe={resolvedPipe} treeIds={treeIds} model={this.props.model} controller={this.props.controller} localResolve={localResolve}
                                     getOrigin={ () => { return [] }}
                  />
                }).toSeq().toArray()
              })}
            </div>
          }


          <hr/>
          <Editor editorState={this.state.editorState}
                  customStyleMap={styleMap}
                  onChange={this.onChange}
                  onCopy={this.onCopy}
                  onFocus={this.onFocus}
                  onBlur={this.onBlur}
                  handlePastedText={this.handlePastedText}
                  handleBeforeInput={this.handleBeforeInput}
                  handleKeyCommand={this.handleKeyCommand}
                  ref={this.setDomEditorRef}
                  // blockRendererFn={myBlockRenderer}
                  readOnly={this.state.activeSuggest !== null || this.state.readOnly}
          />
          <hr/>
          {this.state.inspector ?
              <div>
              <u onClick={()=>this.setState({inspector: false})} style={{float: "right"}}>Inspector</u>
              <ObjectInspector data={currentPipeResult}/>
              </div>
              :
              <div>
                <u onClick={()=>this.setState({inspector: true})} style={{float: "right"}}>HTML</u>
                <div dangerouslySetInnerHTML={{__html: currentPipeResult}}/>
              </div>
          }

          {/*{ pulledBy && <div>*/}
            {/*pulled by:*/}
            {/*{ pulledBy.map((d) => {*/}
              {/*return <div className="pipe-row">*/}
                {/*<u onClick={() => {*/}
                  {/*this.props.controller.setActivePipe(d)*/}
                {/*}}>{d.desc}</u>*/}
                {/*<ObjectInspector*/}
                    {/*data={this.props.model.currentVM._toValue(this.props.pipeHelper.pipeInput.getIn([this.props.model.currentVM.context, d], {}).result)}/>*/}
              {/*</div>*/}
            {/*})}*/}
          {/*</div>}*/}

          {/*<RangeControl pipeHelper={this.state.pipeHelper} editorState={this.state.editorState} focusOffset={this.state.focusOffset}  selfRef={this.storeRangeControlRef} textide={this} />*/}
        </div>
    );
  }
}




