import Convertor from "./convertor"
import {Ptr} from "../ptr"
import {Observer} from "../observer"
import RangeHelper from "./rangeHelper"
import {EditorState, convertFromRaw} from 'draft-js';
import { tmpPipe } from '../base'
import {Map, Set, List} from "immutable"

export default class PipeHelper {
  constructor(pipe, vm, pull, pull2){
    this.pipe = pipe
    this.vm = vm
    this.pull = pull
    this.pull2 = pull2

    let path = []

    let callback = (data) => {
      if (data.pipe === pipe) {
        path.push(data)
      }
    }
    tmpPipe.content[0] = ''
    Observer.subscribe('vm_push_chunk', callback)
    this.initialRaw = this.performPull()
    Observer.unsubscribe(callback)
    tmpPipe.content[0] = '1'
    this.rangeHelper = new RangeHelper(path, pipe)
    this.performPull()
  }

  pipeResult(vm){
    return this.tree.getIn(vm.treeId().concat('result'))
  }

  performPull(){
    let self = this
    this.tree = Map()
    this.pipeInput = Map()
    this.pipeTree = Map()
    this.pullMap = Map()
    this.parentDep = Map()
    this.parentDepResult = Map()
    this.pulledDeps = Set()
    this.pushed = Map()
    this.pulledBy = Map()

    let treeCallback = Observer.subscribe('vm_getdep', (data) => {
      if (data.origin[0] !== tmpPipe && data.requestedPipe !== tmpPipe) {
        let treePath = data.vm.treeId()
            .concat(['origins'])
            .concat(List(data.origin))
            .concat('deps')
            .concat(data.requestedPipe)
            .concat(data.depPipe)
        let ids = self.tree.getIn(treePath, Set())
        self.tree = self.tree.setIn(treePath, ids.add(List(data.requesterId)))
      }
    })

    let resultCallback = Observer.subscribe('vm_result', (data) => {
      let treePath = data.vm.treeId()
      // console.log('vm_result tpath', treePath)

      self.tree = self.tree.setIn(treePath.concat(['result']), data.result)
      self.tree = self.tree.setIn(treePath.concat(['vm']), data.vm)
    })

    let callback = Observer.subscribe('vm_push', (data) => {
      if (0 === data.i && data.pipe === data.block) {
        let key = [data.vm.context]
        if (!self.pipeTree.hasIn(key)){
          self.pipeTree = self.pipeTree.setIn(key, {vm: data.vm, pipe: data.pipe})
        }
      }
    })

    let pullCallback = Observer.subscribe('vm_pull', (data) => {
      if (data.depPipe !== tmpPipe) {
        let key = [data.vm.context, data.originPipe]
        let value = self.pullMap.getIn(key, Set())
        self.pullMap = self.pullMap.setIn(key, value.add(data.depPipe))

        let key2 = [data.vm.context, data.depPipe]
        let value2 = self.pullMap.getIn(key2, Set())
        self.pulledBy = self.pulledBy.setIn(key2, value2.add(data.originPipe))
      }
    })

    let pushResultCallback = Observer.subscribe('vm_push_result', (data) => {
      if (0 === data.i && data.pipe === data.block) {
        self.pipeInput = self.pipeInput.setIn([data.vm.context, data.pipe], data)
      }
    })

    let parentGetDepCallback = Observer.subscribe('vm_parent_getdep', (data) => {
      let key = data.treeId
      let value = self.parentDep.getIn(key, Set())
      self.parentDep = self.parentDep.setIn(key, value.add(data.pipe))

      self.parentDepResult = self.parentDepResult.setIn([data.vm.context, data.pipe], data.result)
    })

    let pullDepCallback = Observer.subscribe('vm_pull_dep', (data) => {
      self.pulledDeps = self.pulledDeps.add(data.dep)
    })

    let pushIndexCallback = Observer.subscribe('vm_push_child', (data) => {
      self.tree = self.tree.setIn(data.vm.treeId().concat('pushed'), data.input)
    })

    let result = this.assuredPull()

    Observer.unsubscribe(pullCallback)
    Observer.unsubscribe(callback)
    Observer.unsubscribe(pushResultCallback)
    Observer.unsubscribe(parentGetDepCallback)
    Observer.unsubscribe(pullDepCallback)
    Observer.unsubscribe(pushIndexCallback)
    Observer.unsubscribe(treeCallback)
    Observer.unsubscribe(resultCallback)

    return result
  }

  assuredPull(){
    let self = this
    let pipePulled = false
    let result

    let callback = Observer.subscribe('vm_push', (data) => {
      if (self.pipe === data.pipe && self.vm.context === data.vm.context) {
        pipePulled = true
      }
    })

    result = Convertor.toDraft(this.pipe, this.vm, this.pull, this.initialRaw)

    if (!pipePulled){
      result = Convertor.toDraft(this.pipe, this.vm, this.pull2, this.initialRaw)
    }

    Observer.unsubscribe(callback)

    return result
  }

  getCurrentBlock(focusOffset, contentState){
    let offset = this.getInitialOffset(focusOffset, contentState)
    if(!(offset >= 0)) return null;
    let left = this.getPtr(offset - 1)
    let right = this.getPtr(offset)
    return (this.rangeHelper.get(left, right)||{}).block
  }

  getInitialOffset(i, content){
    let leftOffset = this._getOffset(i - 1, content)
    let rightOffset = this._getOffset(i , content)
    if (!Number.isInteger(leftOffset) && !Number.isInteger(rightOffset)) return;
    if (Number.isInteger(leftOffset) && Number.isInteger(rightOffset) && leftOffset !== rightOffset -1) return

    return Number.isInteger(leftOffset) ? leftOffset +1 : rightOffset
  }

  _getOffset(index, content){
    if (index < 0 ) return null;
    let key = content.getFirstBlock().getEntityAt(index)
    if (!key) return null
    let data = content.getEntity(key).getData()
    if (data.block && tmpPipe === data.block.condition) return null;
    return data.j
  }

  getPtr(i){
    if (this.initialRaw.entityMap[i]){
      let data = this.initialRaw.entityMap[i].data
      return new Ptr(data.block, data.i)
    }
  }

  getPtrByInitialOffset(i){
    let left = this.getPtr(i - 1)
    let right = this.getPtr(i)
    return this.rangeHelper.get(left, right)
  }

  getFromPtrByInitialOffset(i, layerIndex){
    let left = this.getPtr(i - 1)
    let right = this.getPtr(i)
    return this.rangeHelper.getFrom(left, right, layerIndex)
  }

  getToPtrByInitialOffset(i, layerIndex){
    let left = this.getPtr(i - 1)
    let right = this.getPtr(i)
    return this.rangeHelper.getTo(left, right, layerIndex)
  }

  toDraftRaw(){
    this.raw = this.assuredPull()
    return this.raw
  }

  toDraft(decorator){
    let raw = this.toDraftRaw()
    let content = convertFromRaw(raw)
    let result = EditorState.createWithContent(content, decorator)
    return result
  }

  fromDraft(content, layer){
    return Convertor.fromDraft(content, this, this.initialRaw.blocks[0].text.length, layer)
  }

  updateDraft(content){
    let newContentState = content
    let i = 0

    let callback = (data) => {
      if (data.pipe === this.pipe) {
        let atom = data.block.content[data.i]
        let newData = {
          atom: atom,
          block: data.block,
          pipe: data.pipe,
          i: data.i,
          result: data.result
        }

        let entityKey = newContentState.getFirstBlock().getEntityAt(i)
        if (entityKey) {
          let oldData = newContentState.getEntity(entityKey).getData()
          if (atom !== oldData.atom){
            console.log("++++++++++++++++++++++++data mismatch++++++++++++++++++++++++++++")
            console.log('new, old:', atom, oldData.atom)
            debugger
          }
          newContentState = newContentState.mergeEntityData(entityKey, newData)
        } else {
          newContentState = newContentState.createEntity('TOKEN', 'IMMUTABLE', newData) //content.createEntity
        }

        i++
      }
    }

    Observer.subscribe('vm', callback)

    this.performPull()

    Observer.unsubscribe(callback)
    console.log('update:', this.pipe)
    return newContentState
  }
}



