import {Observer} from "./observer"
import { locationHash, locationHashResult } from "./base"

class RootVM {
  getDep(origin, requestedPipe, requesterId){
    let depPipe = null
    if (requestedPipe === locationHash) depPipe = locationHashResult
    Observer.fire('vm_getdep', {requesterId: requesterId, vm: this, origin: origin, requestedPipe: requestedPipe, depPipe: depPipe})
    return depPipe
  }

  treeId(){
    return []
  }
}

const rootVM = new RootVM()
export { rootVM }



