import {Pipe} from './pipe'
import {VM} from './vm'
import {Dep} from './dep'

const concatB = new Pipe([])
const concatA = new Pipe([{
  push: function(value, vm, origin){
    vm.notifyPull(concatB, concatA)
    return vm.toString(value).concat(new VM(concatB, vm, origin).toValue())
  }
}])
concatA.desc = 'Concatenation'
concatB.desc = 'Concatenation B'

const incB = new Pipe([1])
const incA = new Pipe([{
  push: function(value, vm, origin){
    vm.notifyPull(incB, incA)
    return vm._toValue(value) + new VM(incB, vm, origin).toValue()
  }
}])
incA.desc = 'Increment'

const decB = new Pipe([1])
const decA = new Pipe([{
  push: function(value, vm, origin){
    vm.notifyPull(decB, decA)
    return vm._toValue(value) - new VM(decB, vm, origin).pull()
  }
}])
decA.desc = 'Decrement'

const repipeB = new Pipe([])
const repipeA = new Pipe([{
  push: function(value, vm, origin){
    return new Dep(new VM(repipeB, vm, origin))
  }
}])
repipeA.desc = 'repipe to repipeB pipe'
repipeB.desc = 'repipeB'

const whileCond = new Pipe([])

const bypass = new Pipe([{
  push: (value, vm) => {
    return value
  }
}])
bypass.desc = 'Bypass'

const tmpPipe = new Pipe(["1"])
tmpPipe.desc = 'tmp pipe'

const equialsB = new Pipe([])
equialsB.desc = "Equials To"
const equialsA = new Pipe([{
  push(value, vm, origin) {
    return vm._toValue(value) === new VM(equialsB, vm, origin).toValue()
  }
}])
equialsA.desc = "Equials"

const isEmpty = new Pipe([{
  push(value, vm, origin) {
    return !vm._toValue(value)
  }
}])
isEmpty.desc = "Is Empty"

const hiPipe = new Pipe('hi')
hiPipe.desc = "Hi pipe"

const locationHashResult = new Pipe([{
  push: (value, vm, origin) => {
    return window.location.hash.substr(1).split('&')
        .filter(str => str.length > 0)
        .map(str => {
          let [key, value] = str.split('=');
          return {[key]: decodeURI(value)};
        })
        .reduce((prev, curr) => Object.assign(prev, curr), {});
  }
}])
locationHashResult.desc = "window location result"

const locationHash = new Pipe([{
  push: (value, vm, origin) => {
    console.log('push id:', vm.treeId())
    let depPipe = vm.getDep(origin, locationHash, vm.treeId())
    return new VM(depPipe, vm, []).toValue()
  }
}])
locationHash.desc = "window location"

const fetchB = new Pipe([])
const fetchA = new Pipe([{
  push: (value, vm, origin) => {
    vm.notifyPull(fetchB, fetchA)
    return (vm._toValue(value)||{})[new VM(fetchB, vm, origin).toValue()]
  }
}])
fetchA.desc = "Object fetch"
fetchB.desc = "Object fetch key"

const upcase = new Pipe([{
  push: (value, vm, origin) => {
    return vm._toValue(value).toUpperCase()
  }
}])

const downcase = new Pipe([{
  push: (value, vm, origin) => {
    return vm._toValue(value).toLowerCase()
  }
}])

const terminator = new Pipe([{
  push: (value, vm, origin) => {
    return undefined
  }
}])
terminator.desc = 'Terminator'

const emptyString = new Pipe([{
  push: (value, vm, origin) => {
    return ''
  }
}])
emptyString.desc = 'Empty String'

export { concatA, concatB, incA, incB, decA, decB, whileCond, repipeA, repipeB, bypass, tmpPipe, equialsB, equialsA, hiPipe, locationHash, fetchA, fetchB, upcase, downcase, terminator, locationHashResult, isEmpty, emptyString }



