import React from 'react';
import {CompositeDecorator} from "draft-js"
import PipeSuggest from "./pipe-suggest"
import DepControl from "./depControl"
import ObjectInspector from 'react-object-inspector';
import { tmpPipe} from "../core/base"
import {VM} from "../core/vm"
import Registry from '../core/registry'
import { List, Map } from "immutable"

const style = {
  backgroundColor: '#eee'
}

const TokenSpan = (props) => {
  console.log(`char render++===============================`)

  // const style = getDecoratedStyle(
  //     props.contentState.getEntity(props.entityKey).getMutability()
  // );
  const data = props.contentState.getEntity(props.entityKey).getData()
  return (
      <span className={`atom block-layer-${Registry.getId(data.block.condition)} block-id-${Registry.getId(data.block)} block-index-${data.i}`} data-offset-key={props.offsetkey}
            style={data.pipe.new || data.block.condition === tmpPipe ? {} : style}>
            {props.children}
          </span>
  );
};

const NestedPipe = (props) => {
  const data = props.contentState.getEntity(props.entityKey).getData()
  console.log(`nested pipe render++=============================== ${props.entityKey}`)
  console.log('nested pipe origin:', data.block)
  let treePath = data.vm.treeId().concat('origins').concat(List([data.i, data.block])).concat('deps')
  let depsNode = props.pipeHelper.tree.getIn(treePath)
  let input = <div>
    {(depsNode||Map()).map((resolvedNode, requestedPipe)=>{
      if (resolvedNode.size > 1) {
        throw new Error("tree structure faulure")
      }
      let resolvedPipe = resolvedNode.keySeq().first()
      let treeIds = resolvedNode.getIn([resolvedPipe])

      let localResolve = props.pipeHelper.pipe.getDep([data.i, data.block], requestedPipe) && [data.i, data.block]

      return <DepControl pipeHelper={props.pipeHelper}
                         requestedPipe={requestedPipe}
                         resolvedPipe={resolvedPipe}
                         treeIds={treeIds}
                         model={props.model}
                         controller={props.controller}
                         localResolve={localResolve}
                         getOrigin={() => {
                           let data1 = props.contentState.getEntity(props.entityKey).getData()
                           return [data1.i, data1.block]
                          }
                         }
      />
    }).toSeq().toArray()}
  </div>

  if (props.model.textide.state.activeSuggest === props.entityKey){
    return (
        <div>
          {input}
          <span className={`block-layer-${Registry.getId(data.block.condition)} block-id-${Registry.getId(data.block)} block-index-${data.i}`} style={style}>{props.children}</span>
          <PipeSuggest pipes={props.model.pipes}
                       autoFocus={true}
                       controller={props.controller}
                       model={props.model}
                       onFocusOut={() => {props.controller.setActiveSuggest(null)}}
                       onChange={(event, {suggestion, inlineCreated}) => {
                         event.preventDefault()
                         event.stopPropagation()
                          props.controller.changeActiveSuggest(suggestion)
                         if (inlineCreated) {
                           props.controller.setActiveVM(new VM(suggestion, props.model.currentVM, [data.i, data.block]))
                         } else {
                           props.controller.reload()
                         }
                       }}
          />
        </div>)
  } else {
    let tpath = data.vm.treeId().concat([data.i, data.block, data.atom])
    let result = props.pipeHelper.tree.getIn(tpath.concat('result'))
    return (
        <div>
          {input}
          <div className="pipe-wrapper">
            <span
                className={`block-layer-${Registry.getId(data.block.condition)} block-id-${Registry.getId(data.block)} block-index-${data.i}`}
                style={style}>{props.children}</span>
            <div className="pipe-info" contentEditable={false}>
              <div className="pipe-row inline">
                <u onClick={() => {
                  const data1 = props.contentState.getEntity(props.entityKey).getData()
                  let tpath = data1.vm.treeId().concat([data1.i, data1.block, data1.atom]).concat('vm')
                  let vm = props.pipeHelper.tree.getIn(tpath)
                  props.controller.setActiveVM(vm)
                }}>{data.atom.desc}</u>
                 &nbsp;&nbsp;= <ObjectInspector data={result}/>
              </div>
            </div>
            {/*<div contentEditable={false}>1<span>lala</span></div>*/}
            {/*<input onKeyDown={(e) => {e.preventDefault(); e.stopPropagation()}}/>*/}
          </div>
        </div>
    );
  }
};

const strategyChar = function(contentBlock, callback, contentState) {
  // console.log('ranges')
  // console.log(callback)
  // console.log(contentState.toJS())
  // console.log(contentBlock.toJS())
  contentBlock.findEntityRanges(
      (character) => {
        const entityKey = character.getEntity();
        if (entityKey === null) {
          return false;
        }
        let data = contentState.getEntity(entityKey).getData()
        if (data.block && tmpPipe === data.block.condition){
          return false;
        }
        return 'string' === typeof data.atom
      },
      // callback
      (start, end) => {
        // console.log('start')
        // console.log(start)
        // console.log(end)
        callback(start, end)
      }
  );
}

const strategyNestedPipe = function(contentBlock, callback, contentState) {
  contentBlock.findEntityRanges(
      (character) => {
        const entityKey = character.getEntity();
        if (entityKey === null) {
          return false;
        }
        let data = contentState.getEntity(entityKey).getData()
        // if (data.block && 'tmp' === data.block.condition){
        //   return false;
        // }
        return 'string' !== typeof data.atom
      },
      // callback
      (start, end) => {
        callback(start, end)
      }
  );
}

const getDecorator = (props) => {
  return new CompositeDecorator([
    {
      strategy: strategyChar,
      component: TokenSpan,
      props: props
    },
    {
      strategy: strategyNestedPipe,
      component: NestedPipe,
      props: props
    }
  ])
}

export default getDecorator



