var handlers = []

export class Observer {
  static subscribe(event, fn){
    handlers.push({
      event: event,
      callback: fn
    });
    return fn
  }

  static unsubscribe(fn){
    handlers = handlers.filter(
        function(item) {
          if (item.callback !== fn) {
            return item;
          }
          return false
        }
    );
  }

  static fire(event, data){
    handlers.forEach(function(item) {
      if (item.event === event){
        item.callback(data);
      }
    });
  }
}



