import Override from './override'
import {Block} from "./block"
import {VM} from './vm'

export class Patch extends Block{
  constructor(content, from, to, condition){
    super(content)
    this.from = from
    this.to = to
    this.condition = condition
  }

  valid(parent, origin){
    if (this.condition) {
      let vm = new VM(this.condition, parent, [this.condition])
      vm.notifyPull(this.condition, origin)
      let result = vm.toValue()
      return Override.process(this.condition, result)
    } else {
      return true
    }
  }
}



