import { Map } from 'immutable'
import {Ptr} from '../ptr'

const mkFromI = (i) => {
  return Math.max(i, Math.floor(i)+0.33)
}

const mkToI = (i) => {
  return Math.min(i, Math.floor(i)+0.66)
}

const mkRandom = (fromIwide, toIwide) => {
  let fromI = mkFromI(fromIwide)
  let toI = mkToI(toIwide)

  let delta = toI - fromI
  return fromI + delta/2 - (Math.random()-0.5)*delta/10
}

const MAXI = 0.999999
export default class RangeHelper {
  constructor(path, pipe){
    this.path = path
    this.pipe = pipe
    this.map = Map()
  }

  get(left, right){
    if (!left && !right) return;
    let result = this.map.getIn(this._toKey(left, right))
    if (!result){
      result = this._default(left, right)
      this.set(left, right, result)
    }
    return result
  }

  set(left, right, value){
    this.map = this.map.setIn(this._toKey(left, right), value)
  }

  inserts(range){
    return this.pipe.patches.filter(patch => {
      return patch.condition
          && patch.from.block === patch.to.block
          && patch.from.i === patch.to.i
          && range.fromI < patch.from.i && patch.from.i < range.toI
    })
  }

  toRight(left, right){
    let ranges = this.ranges(left, right)
    let ptr = this.get(left, right)
    let range = ranges.find((range) => { return range.block === ptr.block })
    let inserts = this.inserts(range)
    let index = inserts.findIndex(patch => patch.from.i >= ptr.i)

    if (-1 === index || 0 === inserts.length){
      ptr = this.nextRange(left, right, ranges, ptr)
    } else
    if (inserts.length - 1 === index) {
      ptr = new Ptr(ptr.block, mkRandom(inserts[inserts.length-1].from.i, range.toI))
    } else {
      ptr = new Ptr(ptr.block, mkRandom(inserts[index].from.i, inserts[index+1].from.i))
    }
    this.set(left, right, ptr)
  }

  toLeft(left, right){
    let ranges = this.ranges(left, right)

    let ptr = this.get(left, right)
    let range = ranges.find((range) => { return range.block === ptr.block })
    let inserts = this.inserts(range)
    let index = inserts.findIndex(patch => patch.from.i >= ptr.i)
    if (-1 === index && inserts.length > 0) index = inserts.length

    if (0 === index || 0 === inserts.length){
      ptr = this.prevRange(left, right, ranges, ptr)
    } else
    if (index === 1) {
      ptr = new Ptr(ptr.block, mkRandom(range.fromI, inserts[0].from.i))
    } else {
      ptr = new Ptr(ptr.block, mkRandom(inserts[index-1].from.i, inserts[index].from.i))
    }
    this.set(left, right, ptr)
  }

  nextRange(left, right, ranges, ptr){
    let i = ranges.findIndex((range) => { return range.block === ptr.block }) + 1
    let range = ranges[i]
    if (range) {
      let inserts = this.inserts(range)
      if (inserts.length > 0){
        return new Ptr(range.block, mkRandom(range.fromI, inserts[0].from.i))
      } else {
        return new Ptr(range.block, mkRandom(range.fromI, range.toI))
      }
    }
    else return ptr
  }

  prevRange(left, right, ranges, ptr){
    let i = ranges.findIndex((range) => { return range.block === ptr.block }) - 1
    let range = ranges[i]
    if (range) {
      let inserts = this.inserts(range)
      if (inserts.length > 0){
        return new Ptr(range.block, mkRandom(inserts[inserts.length-1].from.i, range.toI))
      } else {
        return new Ptr(range.block, mkRandom(range.fromI, range.toI))
      }
    }
    else return ptr
  }

  _toKey(left, right){
    return [left && left.block, left && left.i, right && right.block, right && right.i]
  }

  _isInChunk(ptr, chunk){
    return ptr.block === chunk.block && chunk.fromI <= (ptr.i + 1) && (ptr.i + 1) <= chunk.toI
  }

  _default(left, right){
    let ranges = this.ranges(left, right)
    let range = ranges[Math.floor(ranges.length / 2)]
    if (!range){
      debugger
      this.ranges(left, right)
    }
    let i = this._middle(range)
    return new Ptr(range.block, i)
  }

  _middle(range){
    return mkRandom(range.fromI, range.toI)
    // let delta = range.toI - range.fromI
    // return range.fromI + delta/2 - (Math.random()-0.5)*delta/10
  }

  random(fromI, toI){
    let delta = toI - fromI
    return fromI + delta/2 - (Math.random()-0.5)*delta/10
  }

  getFrom(left, right, layerIndex){
    if (!left && !right) return;
    let ranges = this.ranges(left, right)
    let range = ranges.pop()
    let fromI = Math.max(range.fromI, Math.floor(range.fromI)+0.66)
    let toI = range.toI
    if (layerIndex >= 0) {
      let size = (toI - fromI)/10
      fromI = fromI + layerIndex*size
      toI = fromI + size
    }
    return new Ptr(range.block,
        this.random(
            fromI,
            toI
        )
    )
  }

  getTo(left, right, layerIndex){
    if (!left && !right) return;
    let ranges = this.ranges(left, right)
    let range = ranges.shift()
    let fromI = range.fromI
    let toI = Math.min(range.toI, Math.floor(range.toI)+0.33)
    if (layerIndex >= 0) {
      let size = (toI - fromI)/10
      toI = toI - layerIndex*size
      fromI = fromI - size
    }
    return new Ptr(
        range.block,
        this.random(
            fromI,
            toI
        )
    )
  }

  ranges(left, right){
    if (!left && !right) return;
    let leftFound
    let result = []

    function handleSection(chunk) {
      result.push({
        block: chunk.block,
        fromI: left && left.block === chunk.block ? Math.max(left.i + 1, chunk.fromI) : chunk.fromI,
        toI: right && right.block === chunk.block ? Math.min(Math.floor(right.i) + MAXI, chunk.toI+ MAXI) : (chunk.block.content.length === chunk.toI ? chunk.toI + MAXI : chunk.toI)
      })
    }

    for(let k = 0; k < this.path.length; k++ ){
      let current = this.path[k]
      if (left && !leftFound) {
        if (this._isInChunk(left, current)) {
          leftFound = true
          handleSection(current)
          if (!right || this._isInChunk(right, current)) break
        }
      } else {
        handleSection(current)
        if (this._isInChunk(right, current)) {
          break
        }
      }
    }

    return result
  }
}



