import {Patch} from './patch'
import {tmpPipe} from "./base"
import {List} from "immutable"

let VCS = new class {
  changePipe(pipe, fromPtr, toPtr, content, condition){
    if (!fromPtr || !toPtr) {
      debugger
      return
    }
    let patch = new Patch(content, fromPtr, toPtr, condition)
    pipe.addPatch(patch)
    return patch
  }

  cleanTmpPatches(pipe){
    pipe.patches = pipe.patches.filter((patch) => {
      if (patch.condition !== tmpPipe){
        return patch
      }
      return false
    })
  }

  cleanTmpDeps(pipe){
    pipe.deps = pipe.deps.filter((value, key) => {
      return !(key.toArray()[1]||{}).new
    })
  }

  setDeps(pipe, block, inserted){
    let i = 0
    inserted.forEach(item => {
      if (item.block) {
        if (item.deps) {
          pipe.deps = pipe.deps.set(List([i, block]), item.deps)
        }
      }
      i++
    })
  }
}

export { VCS }



