import React from "react"

const Navigation = (props) => {
  const onClick = (id) => {
    let vm = props.pipeHelper.tree.getIn(id.concat('vm'))
    props.controller.setActiveVM(vm)
  }

  const buildView = (tree, id1) => {
    if (0 === tree.length) return
    let pipe, result
    let path = tree.slice()
    let id = id1.slice()

    let head = path.shift()
    id.push(head)
    if (Number.isInteger(head)){
      id.push(path.shift())
      id.push(pipe = path.shift())
      return <ul>
        <li><u onClick={ () => { onClick(id) } }>{pipe.desc}</u></li>
        {
          buildView(path, id)
        }
      </ul>
    } else {
      pipe = head
      result = [<li key={id}>
        <u onClick={ () => { onClick(id) } }>{pipe.desc}</u>
        </li>]
      return result.concat(buildView(path, id))
    }
  }

  const trees = () => {
    if (props.model.currentVM.treeId()[0] === props.model.rootVM.treeId()[0]) {
      return <ul>{buildView(props.model.currentVM.treeId(), [])}</ul>
    } else {
      return <div>
        <ul>{buildView(props.model.rootVM.treeId(), [])}</ul>
        <ul>{buildView(props.model.currentVM.treeId(), [])}</ul>
      </div>
    }
  }

  return (
      <div>
        <h4>Navigator</h4>
        {trees()}
      </div>
  )
}
export default Navigation



