import React from 'react'
import './layers.css'
import Override from "../core/override"
import Registry from '../core/registry'
import {VM} from "../core/vm"

const Layers = (props) => {
  let layers = props.model.currentVM.pipe.layers||[]
  let treeId = props.model.currentVM.treeId()
  let node = props.pipeHelper.tree.getIn(treeId)

  return (
      <div>
        <h4>Layers</h4>
        <ul className="layers">
          <li className={(props.model.currentLayer === null) ? "active block-layer-null" : "block-layer-null"}
              onClick={(e) => {
                props.controller.setActiveLayer(null)
              }}>
            Default layer
          </li>
          {
            layers.map((layer) => {
              let result = node.getIn([layer, 'result'])
              let layerValue = props.model.currentVM._toValue(result)
              let overrideValue = Override.process(layer, layerValue)
              let isOverriden = !layerValue !== !overrideValue
console.log(`overriden: ${isOverriden}`)
              let className = (props.model.currentLayer === layer) ? "active" : ""
              className+=` block-layer-${Registry.getId(layer)}`
              if (isOverriden) className+= ' override'
              return (<li key={layer.desc}
                          className={ className }
                      >
                    <input type="checkbox" onChange={(e) => {props.controller.toggleLayer(layer, e.target.checked, layerValue)}} checked={!!overrideValue} />
                    <u onClick={(e) => {
                      props.controller.toggleLayer(layer, true, layerValue)
                      props.controller.setActiveLayer(layer)
                    }}>
                      {layer.desc}
                    </u>
                    &nbsp;
                    <u onClick={()=>{
                      let tpath = props.model.currentVM.treeId().concat([layer])
                      let vm = props.pipeHelper.tree.getIn(tpath.concat('vm'))
                      if (!vm){
                        vm = new VM(layer)
                      }
                      props.controller.setActiveVM(vm)}
                    }>
                      link
                    </u>
                  </li>
              )
            })
          }
        </ul>
        <style>
          { '.block-layer-null { color: black }' }
          {
            layers.map((layer) => {
              if (layer.color) {
                return `.block-layer-${Registry.getId(layer)} { color: ${layer.color} }`
              }
              return null
            })
          }
          {
            props.model.currentLayer && `.DraftEditor-root { color: ${props.model.currentLayer.color} }`
          }
        </style>
      </div>
  )
}

export default Layers



