import { Map } from 'immutable'
let overrides = Map()

export default class Override {
  static process(action, value){
    if (overrides.has(action)){
      return overrides.get(action)
    } else {
      return value
    }
  }

  static set(action, overrideValue, actualValue){
    if (overrideValue === actualValue){
      if (overrides.has(action)) {
        overrides = overrides.delete(action)
      }
    } else {
      overrides = overrides.set(action, overrideValue)
    }
  }

  static isOverridden(action){
    return overrides.has(action)
  }

  static getOverride(action){
    return overrides.get(action)
  }
}



