import React from 'react'
import Autosuggest from 'react-autosuggest'
import './autosuggest.css'

export default class PipeSuggest extends React.Component {
  constructor(props) {
    super(props);

    let value = props.dep
    if (!value || 'Block' !== value.constructor.name) {
      value = ""
    }

    this.state = {
      value: value,
      suggestions: props.pipes
    };

    this.onSubmit = this.onSubmit.bind(this);
    this.onFocus = this.onFocus.bind(this);
    this.onFocusOut = this.onFocusOut.bind(this);
    this.storeInputReference = this.storeInputReference.bind(this);
    this.getSuggestions = this.getSuggestions.bind(this);
  }

  getSuggestions(value){
    const inputValue = value.trim().toLowerCase();
    const inputLength = inputValue.length;

    return inputLength === 0 ? [] : this.props.pipes.filter(obj =>
        obj.desc.toLowerCase().match(inputValue)
    );
  };


  onSubmit() {
    console.log('submit')
    // this.props.parent.submitCell(this.props.index)
    // this.props.block.content.push('')
    // this.props.parent.update()
  }

  storeInputReference(autosuggest) {
    if (autosuggest !== null) {
      console.log('ref')
      // this.props.inputRef(autosuggest.input)
      this.input = autosuggest.input
      this.input.addEventListener("focus", this.onFocus)
      this.input.addEventListener("focusout", this.onFocusOut)
    }
  }

  onFocus(){
    // this.props.parent.active = this.props.index
    console.log('onfocus')
  }

  onFocusOut(){
    console.log('onfocusout')
    if (this.props.onFocusOut) {
      this.props.onFocusOut()
    }
  }

  render() {
    const { suggestions } = this.state;

    // let value = this.props.block.content[this.props.index].toString();
    let value = this.state.value
    if ('string' !== typeof value){
      value = value.content
    }
    // Autosuggest will pass through all these props to the input.
    const inputProps = {
      autoFocus: this.props.autoFocus,
      placeholder: 'name',
      value: value,
      onChange: (event, { newValue, method }) => {
// event.preventDefault();
// event.stopPropagation()
        console.log('input props onchange')
        // this.props.parent.changeCell(this.props.index, newValue)
        // this.props.block.content[this.props.index]=newValue
        // console.log(this.props.index)
        // console.log(this.props.block.content)
        // this.forceUpdate()
        console.log('onchange')
        console.log(newValue)
        console.log(event)
        // this.state.value = newValue
        this.setState({
          value: newValue
        });
      },
      onKeyDown: (e) => {
        // e.preventDefault()
        // e.stopPropagation()
        console.log('input props onkeydown')
        if ('c' === e.key && e.ctrlKey) {
          let pipe = this.props.controller.createPipe()
          this.setState({
            value: pipe
          });
          this.props.onChange(e, {suggestion: pipe, inlineCreated: true})
        }
      }
    };

    // Finally, render it!
    return (
        <form onSubmit={e => {e.preventDefault(); this.onSubmit()}} style={{display: "inline"}}>
          <Autosuggest
              suggestions={suggestions}
              onSuggestionsFetchRequested={({ value }) => {
                this.setState({
                  suggestions: this.getSuggestions(value)
                });
              }}
              onSuggestionSelected={this.props.onChange}
              onSuggestionsClearRequested={() => {
                this.setState({
                  suggestions: []
                })}
              }
              getSuggestionValue={pipe => {
                return pipe.desc;
              }}
              renderSuggestion={suggestion => (
                  <div>
                    {suggestion.desc}
                  </div>
              )}
              inputProps={inputProps}
              ref={this.storeInputReference}
          />
        </form>
    );
  }
}



