import {Observer} from "../observer"
import {VCS} from "../vcs"
import {tmpPipe} from "../base"
import {List} from "immutable"

export default class Convertor {
  static getEntity(raw, block, i){
    let kv = Object.entries(raw.entityMap).find((kv) => {
      return kv[1].data.block === block && kv[1].data.i === i
    })
    return kv ? kv[1] : false
  }

  static toDraft(pipe, vm, pull, initialRaw){
    var text = '';
    var ranges = [];
    var map = {}
    var i = 0;
    let j = 0
    var callback = (data) => {
      let atom = data.block.content[data.i]
      if (data.pipe === pipe) {
        if ('string' === typeof atom) {
          text+= atom
        } else text += '-';
        // text += data.block.content[data.i];
        ranges.push({offset: i, length: 1, key: i})
        map[i] = {
          type: 'TOKEN',
          mutability: 'IMMUTABLE',
          data: {
            atom: atom,
            vm: data.vm,
            block: data.block,
            pipe: data.pipe,
            i: data.i,
            result: data.result
          }
        }
        if (tmpPipe !== data.block.condition) {
          if (initialRaw) {
            let entity = Convertor.getEntity(initialRaw, data.block, data.i)
            if (entity) {
              map[i].data.j = entity.data.j
            }
          } else {
            map[i].data.j = j
            j++
          }
        }
        i++
      }
    }

    Observer.subscribe('vm', callback)
    let finalCallback = Observer.subscribe('vm_result', (data) => {
      if (data.pipe === pipe) {
        Observer.unsubscribe(callback)
      }
    })
    pull()

    Observer.unsubscribe(finalCallback)

    return {
      blocks: [
        {
          text: (
              text
          ),
          type: 'unstyled',
          entityRanges: ranges,
        },
      ],

      entityMap: map,
    };
  }

  static commit(pipe, fromIndex, toIndex, content){
    debugger
  }


  static fromDraft(content, pipeHelper, initialLength, layer){
    let draftBlock = content.getFirstBlock()
    let text = draftBlock.getText()
    console.log(`fromDraft text: ${text}`)
    console.log('before convert:', pipeHelper.pipe)
    let j = 0;
    let from = null, inserted=[];
    let result = []

    for (let i = 0; i < text.length; i++){
      let entityKey = draftBlock.getEntityAt(i)
      let data
      if (entityKey) {
        data = content.getEntity(entityKey).getData()
      }
      if (entityKey && data.block && tmpPipe !== data.block.condition && !data.block.new){
        //if existed
        if (data.j === j){
          j++
        } else {
          //to remove
          if (null === from) {
            from = j
          }
          j = data.j + 1
        }
        //synced, finish range if opened
        if (null !== from){
          //range finish
          result.push({
            from: from,
            to: data.j,
            inserted: inserted
          })
          from = null
          inserted = []
        }
      } else {
        // to insert
        if (null === from) {
          from = j
        }
        if (data && data.atom){
          inserted.push({
            atom: data.atom,
            block: data.block,
            i: data.i,
            deps: data.pipe && data.pipe.deps.get(List([data.i, data.block]))
          })
        } else {
          inserted.push({
            atom: text[i]
          })
        }
      }
    }
    if ((null !== from) || (j < initialLength)) {
      result.push({
        from: from ? from : j,
        to: initialLength,
        inserted: inserted
      })
    }

    console.log('fromDraft result', result)
    VCS.cleanTmpPatches(pipeHelper.pipe)
    VCS.cleanTmpDeps(pipeHelper.pipe)

    result.forEach((range) => {

      let content = range.inserted.map(item => item.atom)
      let block
      if (pipeHelper.pipe.new) { //!from && !to && 0 === range.from && 0 === range.to
        console.log('looks like new pipe, overwrite it')
        pipeHelper.pipe.content = content
        block = pipeHelper.pipe
      } else {
        let fromPtr, toPtr;
        console.log(range)
        if (range.from === range.to) {
          fromPtr = toPtr = pipeHelper.getPtrByInitialOffset(range.from)
        } else {
          fromPtr = pipeHelper.getFromPtrByInitialOffset(range.from, (layer||{}).index)
          toPtr = pipeHelper.getToPtrByInitialOffset(range.to, (layer||{}).index)
        }
        console.log('fromPtr')
        console.log(fromPtr)
        console.log('toPtr')
        console.log(toPtr)
        console.log(range.inserted)

        block = VCS.changePipe(pipeHelper.pipe, fromPtr, toPtr, content, layer)
      }
      VCS.setDeps(pipeHelper.pipe, block, range.inserted)
    })
    console.log('convert:', pipeHelper.pipe)
    console.log('convert deps:', pipeHelper.pipe.deps.size)
    return pipeHelper.pipe
  }
}



